import { useState, useEffect } from "react";
import { Candidate } from "@/lib/definitions";
import { CandidateCard } from "./candidateCard";
import Link from "next/link";

export const ResultCard = ({
  candidateData,
}: {
  candidateData?: Candidate[];
}) => {
  const [currentCandidate, setCurrentCandidate] = useState<Candidate[] | null>(
    null
  );

  useEffect(() => {
    if (candidateData) setCurrentCandidate(candidateData);
  });

  return (
    <div className="grid md:grid-cols-2 gap-8 grid-cols-1">
      {currentCandidate?.map((candidate,index) => (
        <div>
          <CandidateCard candidateData={candidate}>
            <div className="space-y-4 mt-2">
              <div className="space-y-2">
                <h3 className="font-semibold">Status</h3>
                <div className="flex gap-2">
                  {candidate?.isActive === true ? (
                    <div className="flex items-center gap-2">
                      <div className="w-4 h-4 bg-green-500 rounded-full"></div>
                      <p className="md:text-lg font-semibold text-green-600">Active</p>
                    </div>
                  ) : (
                    <div className="flex items-center gap-2">
                      <div className="w-4 h-4 bg-red-500 rounded-full"></div>
                      <p className="md:text-lg font-semibold text-red-600">Inactive</p>
                    </div>
                  )}
                </div>
              </div>
              <div className="space-y-2">
                <h3 className="font-semibold">Skills</h3>
                <div className="flex flex-wrap gap-2">
                  {[
                    "React",
                    "Java",
                    "Next.js",
                    "Spring Boot",
                    "Backend",
                    "MongoDB",
                  ].map((skill) => (
                    <span
                      key={skill}
                      className="px-3 py-1 bg-gray-200 rounded text-xs"
                    >
                      {skill}
                    </span>
                  ))}
                </div>
              </div>

              <div className="flex items-center gap-2 flex-wrap">
                <h3 className="font-semibold">Action</h3>
                <div className="flex flex-wrap gap-2 text-sm">
                  <button className="px-2 py-1 bg-blue-500 text-white rounded hover:bg-blue-700">
                    View Resume
                  </button>
                  <Link href={`/candidates/${candidate?.id}`}>
                  <button className="px-2 py-1 bg-gray-500 text-white rounded hover:bg-gray-700">
                    View Profile
                  </button>
                  </Link>
                  <button className="px-2 py-1 bg-blue-500 text-white rounded hover:bg-blue-700">
                    Select
                  </button>
                </div>
              </div>
            </div>
          </CandidateCard>
        </div>
      ))}
    </div>
  );
};
